.container{
    height: calc(100vh - 60px);
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.wrapper{
    height: 50vh;
    width: 50vh;
    display: flex;
    flex-direction: column;
    gap: 24px;
    align-items: center;
}

.wrapper > h2{
    font-weight: bold;
    font-size: 36px;
    white-space: nowrap;
}

.wrapper > p{
    font-size: 28px;
    font-weight: 500;
    color: #333;
}

.wrapper > span{
    font-size: 24px;
    color: #555;
}

.emptyCart{
    padding: 0.5rem 1rem;
    color: #fff;
    background-color: rgb(255, 0, 162);
    border-radius: 12px;
    margin-bottom: 0.5rem;
    border: 1px solid transparent;
    cursor: pointer;
}

.emptyCart:hover{
    border-color: rgb(255, 0, 144);
    color: rgb(255, 0, 170);
    background-color: #fff;
}

.emptyCart:disabled{
    background-color: #999;
    color: #fff;
}